.header {
    background-color: #333;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
}

.header h1 {
    font-size: 24px;
    margin: 0;
}

.header ul {
    list-style: none;
    display: flex;
    gap: 20px;
    margin: 0;
}

.header li {
    padding: 0;
    margin: 0;
}

.header a {
    text-decoration: none;
    color: white;
    font-weight: bold;
}

.header a:hover {
    text-decoration: underline;
}