.container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
}

h2 {
    font-size: 24px;
    margin-bottom: 10px;
}

p {
    font-size: 16px;
    margin-bottom: 20px;
}

img {
    max-width: 100%;
    height: auto;
}

.link {
    display: inline-block;
    margin-right: 10px;
    text-decoration: none;
    background-color: #007acc;
    color: #fff;
    padding: 8px 16px;
    border-radius: 5px;
    font-weight: bold;
}

.link:hover {
    background-color: #005a99;
}